import * as React from "react";
import {
  Box, Card, CardContent,
  // CardMedia, 
  Typography
} from "@mui/material";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { useStaticAssets } from "../../hooks/useStaticAssets";
// import FormatQuoteIcon from "@mui/icons-material/FormatQuote";

const TeamMemberCard = ({
  img,
  firstName,
  lastName,
  title,
  description,
  quote
}) => {
  const staticAssets = useStaticAssets();
  const fullName = `${firstName} ${lastName}`;
  // trim the "/images/" part
  const relativePath = img.src.replace("/images/team/", "");
  return (
    <Card
      elevation={6}
      sx={{
        // maxWidth: 345,
        pt: 2
      }}
    >
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          height: 350,
          width: '100%',
        }}
      >
        {/* <CardMedia
          sx={{ height: 350, width: 500 }}
          image={img.src}
          alt="portrait"
          title={fullName}
        /> */}
        <Box
          sx={{
            borderRadius: 1, // 16px rounded corners
            boxShadow: 3, // Material-UI shadow level
            overflow: 'hidden', // Ensure the image does not overflow the rounded corners
          }}
        >
          <GatsbyImage
            alt={`${fullName} portrait`}
            image={getImage(staticAssets.images.team[relativePath])}
          />
        </Box>

      </Box>

      <CardContent>
        <Typography gutterBottom variant="h5" component="div">
          {`${title} ${firstName} ${lastName}`}
        </Typography>
        {/* <FormatQuoteIcon
          sx={{
            ml: "auto",
            mr: "10%",
            color: "text.secondary",
            display: "block"
          }}
        /> */}
        <Typography variant="body2" color="text.secondary" fontStyle="italic">
          {quote}
        </Typography>
        <Box sx={{ py: 2 }}>
          {
            description.map((paragraph, n) => (
              <Typography
                sx={{ py: 1, display: "block" }}
                variant="body3"
                textAlign="left"
                color="text.secondary"
                key={n}
              >
                {paragraph}
              </Typography>
            ))
          }
        </Box>
      </CardContent>
    </Card>
  );
};

export default TeamMemberCard;
